.Footer {
  width: 100%;
  height: 100%;
  background-color: black;
}
.Footer-div {
  margin: 0 auto;
  padding: 1rem;
  width: 85%;
  height: 100%;
}

.FooterCategoryDiv {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  //   border: 2px solid white;
}

.FooterCategory {
  padding: 2rem;
  padding-top: 4rem;
  // border: 2px solid white;
  ul {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    li {
      text-align: left;
      list-style: none;
      padding-left: 0.5rem;
      a {
        color: white;
        font-size: 0.9rem;
        padding: 0.5rem;
      }
    }
  }
}

.FooterIconsDiv {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  //   border: 2px solid white;
  div {
    padding: 1rem;
    a {
      color: white;
    }
    i {
      color: white;
      padding: 1rem;
    }
  }
}

.FooterCopyRightDiv {
  margin: 1rem;
  color: white;
  span {
    padding: 0.5rem;
  }
}

@media only screen and (max-width: 987px) {
  .Footer-div {
    width: 100%;
  }
  .FooterCategory {
    padding: 1rem;
    ul {
      li {
        text-align: center;
        padding-left: 0.2rem;
        a {
          padding: 0.4rem;
        }
      }
    }
  }
}

@media only screen and (max-width: 748px) {
  .FooterCategory {
    ul {
      flex-direction: column;
      li {
        text-align: center;
        padding: 0.5rem;
      }
    }
  }
}

@media only screen and (max-width: 375px) {
  .FooterIconsDiv {
    flex-direction: row;
    // border: 2px solid white;
    div {
      padding: 1rem;
      a {
        color: white;
      }
      i {
        color: white;
        padding: 1rem;
      }
    }
    .FooterIconsDiv-icons {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
}
